import React, { FC } from 'react';
import { Button, Radio, Collapse } from 'antd';
import { CaretRightOutlined } from '@ant-design/icons';
import { copyToClipboard } from '../utils/copy-to-board';
import Palette from '../theme/palette.json';
import './config-panel.less';

const { Panel } = Collapse;

type Props = {
  config: any;
  /** seriesCount   */
  onChange: (config: any) => void;
  /**  */
  onThemeChange: (theme: object) => void;
  style?: React.CSSProperties;
};

export const ConfigPanel: FC<Props> = props => {
  const { style = {}, config, onThemeChange } = props;

  const copyConfig = () => {
    copyToClipboard(JSON.stringify(config || null));
  };

  const onColorsChange = ({ colors10, colors20 }) => {
    onThemeChange({ colors10, colors20 });
  };

  return (
    <div className="config-panel" style={style}>
      <div className="config-panel-title"></div>
      {/* todo  */}
      <div className="">
        <Button onClick={copyConfig}></Button>
      </div>
      <hr />
      {/*  START */}
      <h4></h4>
      <Radio.Group defaultValue={0} className="color-palette-picker">
        {Palette.categorical.map((colors, idx) => {
          return (
            <Radio.Button
              key={`${idx}`}
              value={idx}
              className="color-group"
              onClick={() => onColorsChange(colors)}
            >
              {colors.colors10.map((color, colorIdx) => (
                <span
                  key={`${colorIdx}`}
                  className="color-item"
                  style={{ background: color }}
                />
              ))}
            </Radio.Button>
          );
        })}
      </Radio.Group>
      {/*  END */}
      <hr />
      <Collapse
        bordered={false}
        defaultActiveKey={['1']}
        className="custom-collapse"
        expandIcon={({ isActive }) => (
          <CaretRightOutlined rotate={isActive ? 90 : 0} />
        )}
      >
        <Panel header="" key="1" className="custom-panel">
          <div>
            <span></span>
          </div>
          <div>
            <span></span>
          </div>
        </Panel>
        <Panel header="" key="2" className="custom-panel">
          <div>
            <span></span>
          </div>
          <div>
            <span></span>
          </div>
        </Panel>
        <Panel header="" key="3" className="custom-panel">
          <div>
            <span></span>
          </div>
        </Panel>
        <Panel header=" - Tooltip" key="4" className="custom-panel">
          <div>
            <span>crosshairs</span>
          </div>
        </Panel>
      </Collapse>
    </div>
  );
};
